* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}
body {
  font-size: 30px;
  background-color: #fff;
  font-family: 'Montserrat', sans-serif;
}
.container {
  width: 100%;

  header {
    width: 100%;
    position: relative;
    height: 100vh;
    background-image: url("../images/background.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: #fff;
    z-index: 1;
    overflow: hidden;

    .spacer {
      width: 100%;
      height: 55px;
    }
    .sticky {
      height: 0;
      opacity: 0;
    }
    nav {
      width: 100%;
      height: 55px;
      background-color: #032b2f;
      font-size: 0.9rem;
      position: fixed;
      top: 0;
      left: 0;
      padding: 0 10px 0 10px;
      z-index: 111;
      transition: 0.5s;
      box-shadow: 1px 5px 9px 0px rgba(1, 1, 1, 0.5);

      ul {
        list-style: none;
        width: 100%;
        max-width: 980px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;

        li {
          float: left;
          position: relative;
          font-weight: 700;
          a {color: #ebeced;}
        }
        li::after {
          content: "";
          display: block;
          position: absolute;
          bottom: 12px;
          left: 0;
          width: 0;
          height: 2px;
          background-color: #ebeced;
          transition: 0.2s;
        }
        li:hover::after {
          width: 25px;
        }
      }
      ul::after {
        clear: both;
        content: "";
      }
    }
    h1 {
      font-size: 1.85rem;
      font-weight: 400;
      text-align: center;
      margin-top: 80px;
    }
    h2 {
      font-size: 1.85rem;
      font-weight: 400;
      text-align: center;
      margin-top: 50px;
    }
    h3 {
      margin-top: 100px;
      margin-bottom: 30px;
      font-size: 1.80rem;
      text-align: center;
      position: relative;
      font-weight: 400;
    }
    h3::after {
      content: "";
      display: block;
      width: 150px;
      height: 1px;
      position: relative;
      top: 15px;
      margin: 0 auto;
      background-color: #fff;
    }
    h1 {
      font-size: 2rem;
      font-weight: 500;
      text-align: center;
    }
    .animateSvg {
      animation: animate 4s ease-in-out infinite;
      -webkit-animation: animate 4s ease-in-out infinite;
      -o-animation: animate 4s ease-in-out infinite;
      -moz-animation: animate 4s ease-in-out infinite;
    }
    #svg {
      text-align: center;
      position: absolute;
      left: 50%;
      margin-left: -25px;
      bottom: 30px;
      width: 50px;

      .ar1, .ar2, .ar3, .ar4 {
        fill: rgba(1, 1, 1, 0.1);
      }

      @-webkit-keyframes animate {
        0% {bottom: 30px;}
        50% {bottom: 20px;}
        100% {bottom: 30px;}
      }
      @keyframes animate {
        0% {bottom: 30px;}
        50% {bottom: 20px;}
        100% {bottom: 30px;}
      }
      @-moz-keyframes animate {
        0% {bottom: 30px;}
        50% {bottom: 20px;}
        100% {bottom: 30px;}
      }
    }
  }
  header::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    background-color: rgba(1, 1, 1, 0.4);
    z-index: -1;
  }
}

@media screen and (min-width: 640px) {
  .container{
    header {
      nav {
        font-size: 1.5rem;
        ul {
          li::after {
            bottom: -5px;
            height: 3px;
          }
        }
      }
      h1 {
        font-size: 2.5rem;
        letter-spacing: 5px;
        margin-top: 140px;
      }
      h2 {
        font-size: 2.2rem;
        letter-spacing: 5px;
      }
      h3 {
        font-size: 2rem;
        margin-top: 130px;
      }

    }
  }
}
